import React from "react"
import classnames from "classnames"

import {
  Button,
  Card,
  CardBody,
  FormGroup,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Container,
  Row,
  Col,
} from "reactstrap"

import ModalComponent from '/src/components/modalComponent'

const GuestBookURL =
  "https://script.google.com/macros/s/AKfycbzgWHhvkfDM4JwhYGqFRWC6eUNSZidk9i4fTiDxmwL2aeXrkWmezcX25lEaX0cz8ybfNw/exec"

export default class GuestBook extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      nama: "",
      email: "",
      pesan: "",
      namaFocused: false,
      emailFocused: false,
      pesanFocused: false,
      modal:false,
      submitLoading:false,
    }
  }

  toggleModal = () => this.setState({modal:!this.state.modal})

  onChangeInputNama = e => this.setState({ nama: e.target.value })
  onChangeInputEmail = e => this.setState({ email: e.target.value })
  onChangeInputPesan = e => this.setState({ pesan: e.target.value })

  onSubmit = e => {
    if(!this.state.submitLoading)
    {
        this.setState({submitLoading: !this.state.submitLoading})
        e.preventDefault()

        fetch(GuestBookURL, {
          method: "POST",
          body: new FormData(document.forms["form-guest-book"]),
        })
          .then(res => {
            this.toggleModal();
          })
          .catch(err =>
            alert(
              "Proses penyimpanan gagal, terdapat gangguan. Yuk laporkan issue ini di github kami untuk perkembangan website ini. terima kasih"
            )
          )
          .finally(() => this.setState({submitLoading: !this.state.submitLoading}))
    }
  }

  render() {
    return (
      <>
        <ModalComponent
            title="Berhasil"
            content="Kunjungan anda berhasil kami catat pada sistem database kami. terima kasih banyak sahabat. Salam Pergerakan..."
            isOpen={this.state.modal}
            toggleModal={this.toggleModal}
        />
        <section className="section section-lg pt-lg-0 section-contact-us">
          <Container>
            <Row
              className={
                "justify-content-center " +
                (this.props.isTopFloat ? "mt--300 " : "")
              }
            >
              <Col lg="8">
                <Card className="bg-gradient-secondary shadow">
                  <CardBody className="p-lg-5">
                    <form name="form-guest-book" onSubmit={this.onSubmit}>
                      <h4 className="mb-1">Buku Tamu Pengunjung</h4>
                      <p className="mt-0">
                        Catat kunjungan anda di buku tamu kami dan kirimkan
                        pesan kepada kami.
                      </p>
                      <FormGroup
                        className={classnames("mt-5", {
                          focused: this.state.nameFocused,
                        })}
                      >
                        <InputGroup className="input-group-alternative">
                          <InputGroupAddon addonType="prepend">
                            <InputGroupText>
                              <i className="ni ni-user-run" />
                            </InputGroupText>
                          </InputGroupAddon>
                          <Input
                            placeholder="Nama Lengkap"
                            type="text"
                            name="nama"
                            onFocus={e => this.setState({ nameFocused: true })}
                            onBlur={e => this.setState({ nameFocused: false })}
                            onChange={this.onChangeInputNama}
                          />
                        </InputGroup>
                      </FormGroup>
                      <FormGroup
                        className={classnames({
                          focused: this.state.emailFocused,
                        })}
                      >
                        <InputGroup className="input-group-alternative">
                          <InputGroupAddon addonType="prepend">
                            <InputGroupText>
                              <i className="ni ni-email-83" />
                            </InputGroupText>
                          </InputGroupAddon>
                          <Input
                            placeholder="Alamat Email"
                            type="email"
                            name="email"
                            onFocus={e => this.setState({ emailFocused: true })}
                            onBlur={e => this.setState({ emailFocused: false })}
                            onChange={this.onChangeInputEmail}
                          />
                        </InputGroup>
                      </FormGroup>
                      <FormGroup className="mb-4">
                        <Input
                          className="form-control-alternative"
                          cols="80"
                          name="pesan"
                          placeholder="Tulis sebuah pesan..."
                          rows="4"
                          type="textarea"
                          onChange={this.onChangeInputPesan}
                        />
                      </FormGroup>
                      <div>
                        <Button
                            className="btn-round"
                            color="default"
                            size="lg"
                            type="submit"
                            disabled={this.state.submitLoading}
                        >
                            {this.state.submitLoading && (
                                <>
                                <span class="spinner-grow spinner-grow-sm" role="status" aria-hidden="true"></span>
                                <span class="sr-only">Loading...</span>
                                </>
                            )}
                            &nbsp; KIRIM PESAN
                        </Button>
                      </div>
                    </form>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Container>
        </section>
      </>
    )
  }
}
